@use 'global' as *;

.works {

}

.works__inner {
    max-width: rem(1100);
    width: 100%;
    margin: 0 auto;
    padding: $padding-pc;
}

.works__contents {
    margin-top: rem(24);
    display: flex;
    flex-wrap: wrap;
}


//aタグ
.works__image {
    width: 40%;
    position: relative;
    display: flex;
    flex-grow: 1;
}

.works__image:nth-child(n+3) {
    margin-top: rem(24);
}

.works__image:nth-child(2n) {
    margin-left: rem(24);
}

.works__image::before {
    content:"";
    display: block;
    padding-top: 56.25%;
}
.works__image img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    object-fit: cover;
    width: 100%;
    height: 100%;
    object-position: center;
}

.works__button {
    margin-top: rem(48);
    text-align: center;
}