@charset "UTF-8";
body {
  font-family: "badtyp", sans-serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.about-me__inner {
  max-width: 56.25rem;
  width: 100%;
  margin: 0 auto;
  padding: 25px;
}

.about-me__contents {
  margin-top: 1.5rem;
  display: flex;
}

.about-me__image {
  width: 40%;
}

.about-me__explain {
  padding-left: 2rem;
  width: 60%;
}

.about-me__skills {
  margin-top: 1.5rem;
  text-align: center;
}

.about-me__coding,
.about-me__design {
  padding-left: 0.5rem;
}

.button {
  display: inline-block;
  font-size: 1.5rem;
  color: #fff;
  background-color: blue;
  border-radius: 1.5rem;
  padding: 0.5rem 2rem;
}

.concept__inner {
  max-width: 56.25rem;
  width: 100%;
  margin: 0 auto;
  padding: 25px;
}

.concept__contents {
  margin-top: 1.5rem;
  display: flex;
  flex-wrap: wrap;
}

.contact__inner {
  max-width: 56.25rem;
  width: 100%;
  margin: 0 auto;
  padding: 25px;
}

.contact__contents {
  margin-top: 1.5rem;
  display: flex;
  justify-content: center;
}

.inner {
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (min-width: 768px) {
  .inner {
    max-width: 1150px;
    padding-right: 25px;
    padding-left: 25px;
  }
}

.message {
  padding: 2.5rem;
}

.message__inner {
  text-align: center;
}

.message__text {
  color: #fff;
}

.mv {
  background-color: rgb(28, 18, 223);
}

.mv__content {
  text-align: center;
  padding: 5rem;
}

.mv__title {
  margin-top: 2rem;
  color: #fff;
  font-size: 3.5rem;
  font-family: "badtyp", sans-serif;
}

.mv__message {
  margin-top: 2rem;
  color: #fff;
  font-size: 1.5rem;
}

.section-layout {
  margin-top: 7.5rem;
}

.section-title {
  text-align: center;
}

.section-title__index {
  font-size: 3rem;
  color: blue;
}

.test {
  font-size: 1.25rem;
  background-color: #111;
  color: #fff;
}

.works__inner {
  max-width: 68.75rem;
  width: 100%;
  margin: 0 auto;
  padding: 25px;
}

.works__contents {
  margin-top: 1.5rem;
  display: flex;
  flex-wrap: wrap;
}

.works__image {
  width: 40%;
  position: relative;
  display: flex;
  flex-grow: 1;
}

.works__image:nth-child(n+3) {
  margin-top: 1.5rem;
}

.works__image:nth-child(2n) {
  margin-left: 1.5rem;
}

.works__image::before {
  content: "";
  display: block;
  padding-top: 56.25%;
}

.works__image img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  object-fit: cover;
  width: 100%;
  height: 100%;
  object-position: center;
}

.works__button {
  margin-top: 3rem;
  text-align: center;
}